body{
	background-color:RGBA(183, 160, 174,0.3);
}

.app{
    width:100%;
    height:auto;
    padding-top:3%;
	margin-bottom:5%;
}

.info{
	margin: 10% auto;
	text-align: center;
}

.screen{
	box-sizing: border-box;
	margin:0 auto;
	background-color:#151718;
	width:70%;
	height:250px;
	border-radius:1em 1em;
	box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
	text-align: center;
	padding:5% 2% 0 2%;
}

.languageInfo{
	color:white;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	word-break: break-word; 
}

.options{
	box-sizing: border-box;
	width:100%;
	height:auto;
	display:flex;
	flex-direction: column;
	text-align: center;
	margin-top: 2%;
}

.primary-btn{
		width:20%;
		cursor:pointer;
		padding: 10px 25px;
		margin-top: 5%;
		border-radius: 7px;
		color: #FFFFFF;
		border: 0;
		font-size: .9em;
		font-family: sans-serif;
		cursor: pointer;
		display: inline-block;
		background-color: #43DB9E;
}

.primary-btn:focus{
	outline:0;
}

.btn {
	border: 0;
	transition: .2s opacity ease-in-out;
	padding: 1.2rem 2rem;
	min-width: 50%;
	font-size: 14px;
	font-weight: 600;
	display: inline-block;
	cursor: pointer;
	text-align: center;
	border-radius: 4px;
	user-select: none;
	outline: none;
	touch-action: manipulation;
	color: #fff;
	margin-top:2%;
	outline:0;
}

.btn:hover {
	opacity: .7;
}

.bg-primary {
	background-color: rgb(129, 140, 248);
}

.title{
	font-size:1.2em;
	font-weight: bold;
}

.parag{
	font-size:1.1em;
}

@keyframes spin{
	0% {
		transform:rotate(0deg);
	}

	100% {
		transform:rotate(360deg);
	}
}


@media screen and (max-width:800px){

	.screen{
		width:90%;
		height:200px;
	}

	.btn{
		width:80%;
	}

	.info{
		margin: 30% auto;
	}

	.primary-btn{
		width: 30%;
	}

	.title{
		font-size:1.4em;
	}
	
	.parag{
		font-size:1.4em;
	}
}

@media screen and (max-width:400px){

	.screen{
		width:90%;
		height:200px;
	}

	.btn{
		width:80%;
	}

	.info{
		margin: 30% auto;
	}

	.primary-btn{
		width: 50%;
	}

	.title{
		font-size:1.4em;
	}
	
	.parag{
		font-size:1.4em;
	}
}